import React, {Component} from 'react'
import noteService from '../services/notes'


class Note extends Component {
  constructor(props) {
    super(props)
    this.state = {
      notes: []
    }
  }

  componentDidMount() {
    noteService.getAll().then(notes =>
      this.setState({ notes }) 
   )}
      // <div>
      //   <h2>{note.otsikko} | {note.tagit.map(tagi => tagi + ' ' )} | </h2>
      //   <p>{note.sisalto}</p>
      // </div>

  render() {
    const notesToShow = this.state.notes
    return (
      <div>
        <h1>Muistiinpanot</h1>
        <ul>
          {notesToShow.map(note => 
            <div>
              <div>
                <h2>{note.otsikko} | {note.tagit.map(tagi => tagi + ' ' )} |</h2>
              </div>
              <div>
                {note.sisalto}
              </div>
            </div> 
          )}
        </ul>
      </div>
    )
  }
}

export default Note



